html,
body {
	padding: 0;
	margin: 0;
	background: #fafafa;
	font-family: "Helvetica Neue", arial, sans-serif;
	font-weight: 400;
	color: #444;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-size: 10px;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

#app {
	font-size: 1.6rem;
}
