.header {
	--hamburger_width: 2rem;
	--hamburger_width_transition: width 0.5s ease-in-out;
	--white: #efefef;
	--black: #1b1b1b;
	--highlight: rgb(137, 221, 12);
	background: var(--black);
	padding: 1.6rem;
}

.header h1 {
	color: #efefef;
	text-align: center;
}

.header nav {
	display: flex;
	justify-content: space-around;
	margin: 0.8rem;
}

.header nav a {
	text-decoration: none;
	text-transform: uppercase;
	font-weight: 600;
	color: var(--white);
	font-size: 1.6rem;
}

.header nav a:hover {
	color: var(--highlight);
}

@media (max-width: 767px) {
	.header {
		display: flex;
		flex-direction: row-reverse;
		justify-content: space-between;
		align-items: center;
	}

	.header nav.open {
		position: absolute;
		background: inherit;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		z-index: 0;
		display: flex;
		flex-direction: column;
		align-items: center;
		border: 1px solid var(--white);
	}
	.header nav.closed {
		display: none;
	}

	.hamburger {
		position: relative;
		width: var(--hamburger_width);
		height: 2rem;
		z-index: 1;
	}

	.hamburger div {
		position: absolute;
		top: 1rem;
		width: var(--hamburger_width);
		height: 0.2rem;
		background: var(--white);
		transition: var(--hamburger_width_transition);
	}

	.hamburger div::before,
	.hamburger div::after {
		position: absolute;
		content: "";
		width: calc(var(--hamburger_width) - 10%);
		height: 0.2rem;
		background: var(--white);
		transition: var(--hamburger_width_transition);
	}

	.hamburger div::before {
		top: 0.6rem;
	}

	.hamburger div::after {
		bottom: 0.6rem;
	}

	.hamburger:hover {
		cursor: pointer;
	}

	.hamburger:hover div,
	.hamburger:hover div::before,
	.hamburger:hover div::after,
	.hamburger.open:hover div,
	.hamburger.open:hover div::before,
	.hamburger.open:hover div::after {
		background: var(--highlight);
		width: var(--hamburger_width);
	}

	.hamburger.open div {
		width: calc(var(--hamburger_width) - 10%);
		transition: var(--hamburger_width_transition);
	}

	.hamburger.open div::before,
	.hamburger.open div::after {
		width: var(--hamburger_width);
		transition: var(--hamburger_width_transition);
	}
}
